.catalog-grid {
  padding-top: 24px;
  width: 987px;

  &__content {
    display: flex;
    flex-wrap: wrap;
  }
  &__item {
    margin-bottom: 79px;
  }
}


[data-grid-columns = "3"] {
  .catalog-grid__item {
    width: 31.3%;

    &:not(:nth-child(3n)) {
      margin-right: 3%;
    }
  }
}
[data-grid-columns = "3"] {
  .catalog-grid__item {
    width: 31.3%;

    &:not(:nth-child(3n)) {
      margin-right: 3%;
    }

  }
}

[data-grid-columns = "4"] {
  .catalog-grid__item {
    width: 22.7%;

    &:not(:nth-child(4n)) {
      margin-right: 3%;
    }
  }
}

[data-grid-columns = "5"] {
  .catalog-grid__item {
    width: 17.6%;

    &:not(:nth-child(5n)) {
      margin-right: 3%;
    }
  }
}

.catalog-columns {
  display: flex;
  align-items: center;
  font-weight: 700;
  font-size: 10px;
  text-transform: uppercase;

  &__text {
    margin-right: 3px;
  }

  &__item {
    &:not(:last-child) {
      margin-right: 5px;
    }
  }

  &__btn {
    padding: 0 5px;
    font-size: 10px;
    color: var(--color-gray);

  &:hover {
    color: var(--color-dark);
  }

  &--current {
    color: var(--color-dark);
  }
  }

  &__list {
    display: flex;
    align-items: center;
  }
}

.catalog-props {
  margin-bottom: 15px;
  &__top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 19px;
  }
}
.catalog-choice {
  font-size: 0;
  display: none;
  &__item {
    border-radius: 12px;
    margin-bottom: 5px;
    margin-right: 5px;
    padding: 5px 12px;
    font-size: 13px;
    color: var(--color-tundora);
    background-color: #eee;
    transition: background-color 0.3s, color 0.3s;
    svg {
      margin-left: 5px;
      width: 8px;
      height: 9px;
      fill: currentColor;
      pointer-events: none;
    }

    &:hover {
      color: var(--color-white);
      background-color: var(--color-dark);
      transition: background-color 0.3s, color 0.3s;
    }
  }

  &__clear {
    position: relative;
    font-weight: 500;
    font-size: 13px;

    &::before {
      content: "";
      position: absolute;
      left: 0;
      bottom: -5px;
      width: 100%;
      height: 2px;
      background-color: currentColor;
      transition: width 0.15s;
   }
  }
}

.custom-select {
  position: relative;
  &__dropdown {
    display: none;
    position: absolute;
    left: 0;
    top: calc(100% + 2px);
    width: 100%;
    min-width: 115px;
    padding: 10px;
    z-index: 20;
    background-color: var(--color-white);
  }
  &__item {
    cursor: pointer;
    &:not(:last-child) {
      margin-bottom: 10px;
    }

    &:hover {
      color: var(--color-tundora);
      transition: color 0.3s;
    }
  }

  &__top {
    position: relative;
    font-weight: 700;
    font-size: 10px;
    padding-bottom: 12px;
    text-transform: uppercase;
    border-bottom: 1px solid #e5e5e7;
    padding-right: 34px;
    cursor: pointer;

    &::after {
      content: "";
      position: absolute;
      right: 0;
      top: 50%;
      margin-top: -6px;
      transform: translateY(-50%);
      width: 10px;
      height: 6px;
      background-image: url(../../img/arrow-down.png);
      background-position: center;
      background-repeat: no-repeat;
      background-size: cover;
    }
  }

  &--open {
    .custom-select__top {
      &:after {
        transform: translateY(-50%) rotate(180deg);
      }
    }

    .custom-select__dropdown {
      display: block;
    }
  }


}
