.main-products {
  &__container {
    padding-top: 75px;
    padding-bottom: 30px;
  }

  &__nav {
    display: flex;
    justify-content: center;
    margin-bottom: 45px;
  }

  &__item {
    margin: 0 18px;
  }

  &__btn {
    font-size: 24px;
    &--current {
      &::before {
        width: 100%;
      }
    }
  }

  &__btn-center {
    text-align: center;
  }

  &__more {
    display: inline-flex;
    flex-direction: column;
    align-items: center;
    margin-top: 92px;
    color: var(--color-gray);
    font-size: 13px;

    span {
      display: inline-block;
      margin-bottom: 11px;
    }

    svg {
      width: 12px;
      height: 21px;
      fill: var(--color-dark);
    }
  }
}

.products-grid {
  display: flex;
  flex-wrap: wrap;

  &__item {
    margin-bottom: 80px;

    &:not(:nth-child(5n)) {
      margin-right: 30px;
    }
    &:nth-last-child(-n+5) {
      margin-bottom: 0;
    }
  }
}
