.banners {
  &__container {
    padding-top: 50px;
    padding-bottom: 30px;
  }

  &__list {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__item {
    flex-shrink: 0;
    width: 33%;

    &:not(:last-child) {
      margin-right: 0.6%;
    }
  }
}

.banners-article {
  background-color: #e9e4df;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  padding: 54px 60px;
  padding-right: 45%;
  min-height: 290px;
  height: 100%;

  &--midseason {
    background-image: url(../img/midseason.jpg);
  }
  &--summer {
    background-image: url(../img/summer.jpg);
  }
  &--accs {
    background-image: url(../img/accs.jpg);
  }


  &__title {
    margin: 0;
    margin-bottom: 20px;
    font-size: 36px;
  }
}
