.breadcrumbs {
  &__list {
    display: flex;
    align-items: center;
    padding-top: 36px;
    padding-bottom: 25px;
    border-bottom: 1px solid #eee;
  }
  &__item {
    position: relative;



    &:not(:last-child) {
      margin-right: 35px;

      &::after {
        content: "/";
        position: absolute;
        right: -20px;
        top: 50%;
        transform: translateY(-50%);
        font-size: 10px;
        color: var(--color-gray);
      }
    }
  }
  &__link {
    font-weight: 500;
    font-size: 10px;
    text-transform: uppercase;
    color: var(--color-gray);
    &--current {
      color:var(--color-dark);
    }
  }
}
