html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-weight: 500;
  font-family: "Jost", sans-serif;
  color: var(--color-dark);
  background-color: var(--color-white);
}

/* .stop-scroll {
  @include stop-scroll;
} */

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.error {
  border: 2px solid #ff4019 !important;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  /* -webkit-text-size-adjust: 100%; */
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
  color: inherit;
}

.site-container {
  overflow: hidden; //     position: sticky -   
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  /* max-width: var(--container-width); */
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.main-link {
  position: relative;
  font-size: 15px;
  color: var(--color-dark);

  &::before {
     content: "";
     position: absolute;
     left: 0;
     bottom: -5px;
     width: 0;
     height: 2px;
     background-color: currentColor;
     transition: width 0.15s;
  }

  &--white {
    color: var(--color-white);
  }

  &:hover {
    &::before {
      width: 100%;
    }
  }
}

.container-narrow {
  max-width: 1406px;
}
