  display: flex;
  align-items: center;
}

.catalog-props {
  margin-bottom: 15px;
}
.catalog-props__top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 19px;
}

.catalog-choice {
  font-size: 0;
  display: none;
}
.catalog-choice__item {
  border-radius: 12px;
  margin-bottom: 5px;
  margin-right: 5px;
  padding: 5px 12px;
  font-size: 13px;
  color: var(--color-tundora);
  background-color: #eee;
  transition: background-color 0.3s, color 0.3s;
}
.catalog-choice__item svg {
  margin-left: 5px;
  width: 8px;
  height: 9px;
  fill: currentColor;
  pointer-events: none;
}
.catalog-choice__item:hover {
  color: var(--color-white);
  background-color: var(--color-dark);
  transition: background-color 0.3s, color 0.3s;
}
.catalog-choice__clear {
  position: relative;
  font-weight: 500;
  font-size: 13px;
}
.catalog-choice__clear::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: -5px;
  width: 100%;
  height: 2px;
  background-color: currentColor;
  transition: width 0.15s;
}

.custom-select {
  position: relative;
}
.custom-select__dropdown {
  display: none;
  position: absolute;
  left: 0;
  top: calc(100% + 2px);
  width: 100%;
  min-width: 115px;
  padding: 10px;
  z-index: 20;
  background-color: var(--color-white);
}
.custom-select__item {
  cursor: pointer;
}
.custom-select__item:not(:last-child) {
  margin-bottom: 10px;
}
.custom-select__item:hover {
  color: var(--color-tundora);
  transition: color 0.3s;
}
.custom-select__top {
  position: relative;
  font-weight: 700;
  font-size: 10px;
  padding-bottom: 12px;
  text-transform: uppercase;
  border-bottom: 1px solid #e5e5e7;
  padding-right: 34px;
  cursor: pointer;
}
.custom-select__top::after {
  content: "";
  position: absolute;
  right: 0;
  top: 50%;
  margin-top: -6px;
  transform: translateY(-50%);
  width: 10px;
  height: 6px;
  background-image: url(../../img/arrow-down.png);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.custom-select--open .custom-select__top:after {
  transform: translateY(-50%) rotate(180deg);
}
.custom-select--open .custom-select__dropdown {
  display: block;
}

.footer__content {
  padding: 0 250px;
  padding-top: 98px;
  padding-bottom: 0;
}
.footer__top {
  display: flex;
  justify-content: space-between;
  padding-bottom: 95px;
}
.footer__column:nth-child(2) {
  margin-left: -60px;
}
.footer__item:not(:last-child) {
  margin-bottom: 17px;
}
.footer__list--row {
  display: flex;
  align-items: center;
}
.footer__list--row .footer__item {
  margin-bottom: 0;
}
.footer__list--row .footer__item:not(:last-child) {
  margin-right: 43px;
}
.footer__link {
  font-size: 13px;
  color: var(--color-tundora);
}
.footer__link--row {
  font-weight: 700;
  color: var(--color-dark);
}
.footer__title {
  margin: 0;
  margin-bottom: 37px;
  font-weight: 700;
  font-size: 16px;
  text-transform: uppercase;
}
.footer__form {
  margin-bottom: 27px;
  transform: translateY(3px);
  width: 646px;
}
.footer__text {
  font-weight: 500;
  font-size: 13px;
  color: var(--color-tundora);
}
.footer__bottom {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-top: 1px solid #eee;
  padding: 40px 0;
}
.footer__social {
  margin-right: 11px;
}

.subscribe-form {
  position: relative;
}
.subscribe-form__title {
  margin: 0;
  margin-bottom: 38px;
  font-weight: 500;
  font-size: 13px;
  color: var(--color-tundora);
}
.subscribe-form__field {
  display: block;
  border-bottom: 2px solid var(--color-dark);
  padding-bottom: 8px;
  width: 100%;
}
.subscribe-form__field input {
  width: 100%;
  padding-right: 80px;
  border: none;
  font-weight: 500;
  font-size: 13px;
  color: var(--color-dark);
}
.subscribe-form__field input::placeholder {
  color: rgba(69, 69, 69, 0.5);
}
.subscribe-form__btn {
  position: absolute;
  right: 0;
  bottom: 0;
  z-index: 2;
  padding: 13px 10px;
  font-weight: 500;
  font-size: 13px;
  color: var(--color-dark);
}

.social {
  display: flex;
  align-items: center;
}
.social__item:not(:last-child) {
  margin-right: 29px;
}
.social__link {
  display: block;
  width: 16px;
  height: 16px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}
.social__link--tw {
  background-image: url("../../img/tw.png");
}
.social__link--fb {
  background-image: url("../../img/fb.png");
}
.social__link--in {
  background-image: url("../../img/inst.png");
}
.social__link--pin {
  background-image: url("../../img/pin.png");
}