.hero {
  position: relative;
  /* height: calc(100vh + 143px); */
  height: 1080px;
  min-height: 700px;
  background-color: var(--quill-gray);

  &::before {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background-color: rgba(#000, 0.05);
  }

  &__container {
    position: relative;
    height: 100%;
  }

  &__title {
    margin: 0;
    margin-bottom: 35px;
    max-width: 540px;
    color: var(--color-dark);
    font-weight: 700;
    font-size: 72px;
  }

  &__descr {
    margin: 0;
    font-size: 18px;
  }

  &__image {
    position: absolute;
    right: 220px;
    bottom: 0;
    max-height: 90%;
  }

  &__content {
    padding: 0 250px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    height: 100%;
  }
}


.banner-slider {
  width: 100%;
  height: 100%;
  overflow: hidden;

  .swiper-wrapper {
    height: 100%;
  }
  .swiper-slide {
    height: 100%;
  }
}

.banner-pag {
  bottom: 45px!important;
  .swiper-pagination-bullet {
    margin: 0 12px !important;
    width: 6px;
    height: 6px;
    background-color: var(--color-white);
    opacity: 0.5;
  }

  .swiper-pagination-bullet-active {
    position: relative;
    background-color: var(--color-dark);
    opacity: 1;

    &::before {
      content: '';
      position: absolute;
      left: 50%;
      top: 50%;
      width: 20px;
      height: 20px;
      border-radius: 20px;
      background-color:#fff;
      transform: translate(-50%, -50%);
      z-index: -1;
    }
  }
}


.marketing {
  position: absolute;
  z-index: 5;
  display: flex;
  padding: 10px;
  bottom: 50px;
  left: 40px;
  width: 280px;
  box-shadow: 0 5px 30px tgba(0, 0, 0, 0.1);
  background-color: var(--color-white);
  transform: translateX(-150vw);
  transition: transform 0.3s;

  &--visible {
    transform: translateX(0);
  }

  &__image {
    flex-shrink: 0;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__text {
    margin-left: 10px;
  }
  &__descr {
    margin-bottom: 5px;
  }
  &__when-from {
    padding-right: 40px;
  }
  &__descr, &__when-from {
    display: block;
    font-size: 11px;
    color: var(--color-gray);
  }

  &__title {
    margin: 0;
    padding-right: 30px;
    font-size: 15px;
    min-height: 34px;
    margin-bottom: 7px;
  }

  &__close {
    position: absolute;
    right: 10px;
    top: 10px;
    padding: 0;
    width: 10px;
    height: 10px;
    background-image: url("../img/close.png");
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
  }
}
