.header {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 100;
    &__container {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding-top: 36px;
      padding-bottom: 36px;
    }

    &__shop-nav {
      margin-left: 250px;
    }
}

.logo {
    display: inline-block;
    width: 117px;
    height: 22px;
    svg {
        display: block;
        width: 100%;
        height: 100%;
        fill: var(--color-dark);
        object-fit: cover;
    }
}

.catalog-header {
  padding-top: 38px;
}
