.hero-catalog {
  padding-top: 130px;
  &__slider {
    position: relative;
    background-color: #f8f8f8;
    overflow: hidden;
    width: 100%;
  }
}

.catalog-slide {
  padding: 108px;
  /* background-image: url(../../img/catalog-girl.png); */
  &__title {
    position: relative;
    margin: 0 0 15px 0 !important;
    max-width: 375px;
    font-weight: 700;
    font-size: 36px;
    margin: 0;
    z-index: 2;
  }

  &__descr {
    position: relative;
    margin: 0;
    margin-bottom: 40px;
    max-width: 390px;
    font-size: 15px;
    line-height: 28px;
    color: var(--color-tundora);
    z-index: 2;
  }

  &__img {
    position: absolute;
    bottom: 0;
    right: 0;
    z-index: 1;
  }

  &__btns {
    position: relative;
    display: flex;
    align-items: center;
    z-index: 2;
  }
  &__link {
    display: inline-block;
    padding: 16px 43px;
    font-size: 13px;

    &--dark {
      border: 1px solid var(--color-dark);
      color: var(--color-white);
      background-color: var(--color-dark);
    }
    &--light {
      border: 1px solid var(--color-dark);
      color: var(--color-dark);
      background-color: transparent;
    }

    &:not(:last-child) {
      margin-right: 15px;
    }
  }
}

.hero-prev-btn,
.hero-next-btn {
  position: absolute;
  top: 50%;
  left: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 48px;
  height: 48px;
  color: var(--color-dark);
  border: 2px solid var(--color-dark);
  transform: translateY(-50%);
  z-index: 2;
  transition: all 0.3s;

  svg {
    width: 40px;
    height: 20px;
    fill: currentColor;
    transition: all 0.3s;
  }

  &:hover {
   background-color: var(--color-dark);
   transition: all 0.3s;

   svg {
     fill: var(--color-white);
   }
  }
}

.hero-next-btn {
  left: auto;
  right: 30px;
}
